
import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import XSwitch from 'c%/switch.vue'
import MyRater from 'c%/MyRater.vue'
import MyPopupPicker from 'c%/PopupPicker.vue'
import SelectCustomer from 'c%/SelectCustomer.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'


const emptyObject = {
bid: 0,
cu_id: 0,				//客户ID
is_connect: 1,			//是否有效沟通0：无效， 1：有效
followup_did: 0,		//跟进方式 字典ID（QQ、电话等）
content: '',			//跟进内容
is_promise: 0,			//是否承诺到访
promise_did: 0,			//承诺到访类型 字典ID
promise_int_day: '', 	//承诺到访日期
is_visit: 0,			//是否到访
visit_int_day: '',		//实际到访日期
next_follow_time: '', 	//下次跟进日期  0为待定
intention_level: 3,		//意向级别
customer_status_did: 0, //客户状态字典ID
}

export default{
mixins: [ common ],
components:{
Cell,
Group,
MyRater,
Datetime,
XButton,
XSwitch,
XTextarea,
MyPopupPicker,
SelectCustomer
},
data () {
return {
info: util.copy(emptyObject)
}
},
mounted () {
this.initInfo()
this.initCustomer()
},
methods: {
initCustomer () {
let cu_id = this.$route.query.cu_id
if(cu_id){
this.info.cu_id = cu_id
}
},
initInfo () {
let followupList = this.getPopupDicts('followup')
if(followupList.length){
this.info.followup_did = followupList[0].key
}
},
handleSubmit () {
this.info.bid = this.bid$[0]
if(this.check()){
let uri = 'customers/'+this.info.cu_id+'/followup'
this.$rest(uri).post(this.info)
.success(data=>{
this.toast('添加成功','sucess')
this.$router.push({path: '/recruit/following'})
}).error(response=>{
this.toast(response.body.message||'添加失败','warn')
})
}
},
check () {
let info = this.info
if(!info.cu_id){
this.toast('请选择客户','warn')
return false
}
if(!info.customer_status_did){
this.toast('请选择客户状态','warn')
return false
}
if(!info.followup_did){
this.toast('请选择沟通类型','warn')
return false
}
if(!info.content){
this.toast('请输入沟通内容','warn')
return false
}
if(info.next_follow_time&&moment(info.next_follow_time).add(1,'days').isBefore(moment(new Date()))){
this.toast('下次跟进时间必须大于当前时间','warn')
return false
}
return true
},
getPopupDicts (field) {
let list = this.dicts(field),
result = []
if(list&&list.length){
list.forEach(l=>{
result.push({
key: l.did,
value: l.title
})
})
}
return result
}
},
computed: {
startDate () {
return moment(new Date()).format('YYYY-MM-DD')
}
}
}



