
import util from '@/libs/util'
import {_} from '@/libs/util'
import common from '@/libs/common.mixin'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


const resName   = 'customers'
const idField   = 'cu_id'
const textField = 'name'

export default{
mixins: [ common ],
components: {
Cell,
Popup,
Flexbox,
FlexboxItem
},
props: {
width: {
type: String,
default: '80%'
},
title: {
type: String,
default: '选择学员'
},
value: {
type: [String,Number,Array],
default: []
}
},
data () {
return {
popupShow: false,
model: [],
check_all: false,
data: [],
search_value: '',
idField: idField,
pageSize: 100,
textField: textField
}
},
watch:{
popupShow: function (val) {
if(val){
this.model = util.copy(this.value)
}
},
value: function (val) {
this.model = util.copy(val)
},
model: function (val) {
this.check_all = (this.model.length === this.data.length)
}
},
mounted () {
this.dr().getData(resName)
.then(data=>{
this.data = data.list
})
},
methods: {
getClass (row) {
return {[`active`]: this.model==row[idField]}
},
ok () {
this.$emit('input',this.model)
this.popupShow = false
},
reset () {
if(_.isArray(this.value)){
this.model = []
}else{
this.model = 0
}
},
close () {
this.model = util.copy(this.value)
this.popupShow = false
},
checkAll () {
this.check_all = !this.check_all

this.model = []
if(this.check_all){
this.data.forEach(b=>{
this.model.push(b[idField])
})
}

},
toggleCheck (row) {
if(this.multiple){
let index = this.model.indexOf(row[idField])
if(index>-1){
this.model.splice(index,1)
}else{
this.model.push(row[idField])
}
}else{
this.model = row[idField]
}
},
getStudentName (id) {
let data = this.data,
result = ''
if(data.length>0&&id>0){
let obj = data.find(s=>s[idField]==id)
if(obj){
result = obj[textField]
}
}
return result
}
},
computed: {
scrollTop () {
/*vux-header + x-popup-header + x-popup-search*/
return this.headerHeight + 50 + 39
},
scrollHeight () {
/*vux-header + x-popup-header + x-popup-search + x-popup-footer*/
return this.headerHeight + 50 + 39 + 40
},
multiple(){
if(Array.isArray(this.value)){
return true
}
return false
},
labelText () {
let model = this.model,
result = '请选择'
if(this.multiple){
if(model.length>0){
result = this.getStudentName[0]
}
if(model.length>1){
result += '等'+model.length+'人'
}

}else{
result = this.getStudentName(model)
}
return result
},
filter_data () {
let val = this.search_value,
data = this.data
if(!/^\s*$/.test(val)){
let filter = (item) => {
return item.pinyin_abbr.indexOf(val)>-1||item.pinyin.indexOf(val)>-1||item[textField].indexOf(val)>-1
}
return data.filter(filter)
}
return data

},
data_uri () {
return util.sprintf('customers?follow_eid=%s&is_reg=0',this.eid$)
}
}
}



